
.view_box {
    width: 410px;
    min-height: 330px;
    background-color: rgb(255, 255, 255);
    margin: 16px;
    border-radius: 18px;
    overflow: hidden;
}

.view_img {
    margin: 0;
    width: 410px;
    height: 270px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.img {
    width: 390px;
    height: 250px;
    border-radius: 18px;
    position: absolute;
}

.view_label {
    margin: 0;
    width: 410px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 18px;
}

h3 {
    margin-left: 20px;
    color: rgb(40, 40, 40);
}

#view_sav_btn {
    position: relative;
    left: 160px;
    top: -92px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background: rgba(160, 160, 166, 0.6);
    backdrop-filter: blur( 3px );
    border: none;
    z-index: 5;
}

#view_sav_btn:hover {
    transition: .6s ease;
    background: rgba(160, 160, 160);
}

#view_sav_icon  {
    font-size: 22px;
    color: #ffffff;
}

#view_more_icon {
    width: 35px;
    height: 35px;
    font-size: 34px;
    color: rgb(40, 40, 40); 
    cursor: pointer;
}

.view_more_btn { 
    width: 45px;
    height: 45px;
    background: none;
    border: 3px solid transparent;
    border-radius: 50%;
    cursor: pointer;
    margin-right: 20px;
    transition: .2s ease;
}
.view_more_btn:hover {
    border: 3px solid #656565;
}

