

body {
  background-color: rgba(215, 215, 215, 0.5);
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
 
#view_recipe {
  width: 40%;
  height: 100vh;
  background-color: rgb(190, 190, 190);
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 10;
}

.show {
  transform: translate(0%, 0%);
  transition: 0.6s ease;
}

.hide {
  transform: translate(120%, 0%);
  transition: 0.6s ease;
}

.close_btn {
  position: absolute;
  right: 102%;
  top: 45%;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  background: rgba(140, 140, 140, 0.65);
  backdrop-filter: blur( 2px );
  border: none;
  color: #ffffff;
}

.close_btn:hover {
    transition: .6s ease;
    background: rgb(145, 145, 145);
}

#close_icon {
  font-size: 24px;
    color: #ffffff;
}


.search_form {
  width: 100%;
  height: max-content;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 15px 5px;
}

.search_input {
  min-width: 450px;
  max-width: 600px;
  height: 25px;
  padding: 5px 5px 5px 8px;
  font-size: 18px;
  border: 3px solid #979797;
  border-radius: 10px;
}

.search_submit {
  width: 80px;
  height: 40px;
  margin-left: 20px;
  border-radius: 10px;
  color: #ffffff;
  cursor: pointer;
  background: rgba(133, 133, 138, 0.6);
  backdrop-filter: blur( 3px );
  border: 3px solid transparent;
  transition: .4s ease;
}
.search_submit:hover {
  border: 3px solid #5a5a5a;
  background: rgba(160, 160, 160);
}

.recipes_view_sec {
  width: 100%;
  height: max-content;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
}